
.container {
    max-width: 80vw;
    margin-inline: auto;
}


.ff-poppins {font-family: 'Poppins', sans-serif;}

.text-bold{font-weight: 700;}
.text-center{text-align: center;}
.text-col-1{color:var(--prim-col-1)}

.d-flex{display:flex;}
.flex-direction-column {flex-direction: column;}




.d-grid{display:grid;}
.gap-25{gap:25px;}
.grid-row-1{grid-template-rows: 1fr;}
.grid-col-1{grid-template-columns: 1fr;}
.grid-col-2{grid-template-columns: 1fr 1fr;}
.grid-col-2-auto{grid-template-columns: auto auto}
.grid-col-4{grid-template-columns: 1fr 1fr 1fr 1fr;}



.grid-wrap{grid-template-columns: repeat(auto-fill, minmax(250px,1fr))}

.align-items-center{align-items: center;}
.justify-items-center{justify-items: center;}
.justify-content-center{justify-content: center;}
.justify-content-end{justify-content: end;}

.justify-self-end{justify-self: end;}
.justify-self-center{justify-self: center;}
.align-self-end{align-self: end;}


.col-span-2{grid-column: auto / span 2;}



